
/* The navbar */
.topnav {
  overflow: hidden;
}

/* Navbar links */
.topnav a {
  float: left;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;

  padding-left: 58px;
  
font-family: Montserrat;
font-style: normal;
font-weight: normal;
font-size: 25px;
line-height: 30px;

color: #FFFFFF;

}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

